.projects-container{
    text-align: center;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-around;
}

.project-card{
    width: 40%;
    height: 500px;
    padding: 10px;
    margin: 10px;
    border: 1px solid var(--light);
    transition: 0.4s;

    position: relative;
    cursor: var(--pointer-OL);
}

.product-card-title{
    opacity: 0;
    width: 70%;
    position: absolute;
    top: 50px;
    left: -30px;
    background-color: var(--dark);
    color: var(--light);

    border-radius: 10px 0 0 10px;
    transition: 1s;
}

.project-card:hover .product-card-title{
    background-color: var(--primary);
    color: var(--light);
    width: 30%;
    opacity: 100%;
}

.project-card:hover{
    border: 1px solid var(--primary);
    border-radius: 5px;
}

.project-img-container{
    width: 100%;
    height: 500px;
    overflow: hidden;
}

.project-img{
    width: 100%;
    filter: grayscale(100%);
    transition: 1s;
}
.project-card:hover .project-img{
    filter: grayscale(0%);
    transform: translateY(-200px);
}

@media (max-width:1000px)
{
    .project-card,
    .project-img-container{
        height: 150px;
        margin: 10px 0px;
    }

    .project-img-container{
        margin: 0px;
    }

    .project-card{
        margin: 10px 0;
        padding: 2px;
    }
    .project-card:hover .product-card-title{
        top: 0;
        left: 0;
        font-size: 5px;
        background-color: var(--primary);
        border-radius: 0;
        width: 100%;
        height: 100%;
        opacity: 100%;
    }
}