.portfolio{
    margin: 100px 0;
}

.projects-section{
    margin: 50px auto;
}

.portfolio-button{
    padding: 20px 60px;
    background-color: var(--primary);
    margin: 20px auto;
    box-shadow: 10px 10px var(--light);
    text-decoration: none;
    color: var(--light);

    transition: 0.4s;
}

.portfolio-button:hover{
    background-color: var(--light);
    box-shadow: 0px 0px 20px var(--primary);
    color: var(--primary);
}