.brands-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
}

.brand-heading,
.brand-logo-list{
    width: 50%;
}

.brand-logo-list{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-around;
    margin: 50px;
}

.brand-logo-container{
    width: 80px;
    height: 80px;
    margin: 10px;
    padding: 20px;
    cursor: var(--pointer);
}
 
.brand-logo{
    width: 70px;
    height: 70px;
    opacity: 50%;
    filter: grayscale(100%);

    transition: 0.4s;
}

.brand-logo-container:hover .brand-logo{
    transform: scale(1.5);
    filter: grayscale(0%);
    opacity: 100%;
}

@media(max-width: 800px){
    .brand-heading,
    .brand-logo-list{
        width: 90%;
    }

    .brand-logo-list
    {
        flex-wrap: nowrap;
        overflow-x: auto;
    }

    .brand-logo-container{
        width: 50px;
        height: 50px;
    }
} 